:root{--blue:#25008b;--darkteal:#4b1c77;--red:#f05075;--teal:#2ea1ab}body{font-family:Open Sans,sans-serif}body,button:hover{background-color:var(--blue);color:var(--teal)}button{background-color:var(--teal);border:none;color:var(--blue);cursor:pointer;font-family:Open Sans,sans-serif;outline:none;text-align:center;-webkit-transition:all .8s ease;transition:all .8s ease}button:hover:after,button:hover:before{-webkit-transition:all .8s ease;transition:all .8s ease;width:100%}em{font-size:.7em;font-style:italic}